*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: aliceblue;
}

.container {
  margin: auto;
  max-width: 75%;
}

.mt-3{
  margin-top: 3em;
}

.card {
  padding: 15px;
}

.card h1 {
  text-align: center;
  color: #444;
}

#drum-machine {
  background-color: #FFF;
  padding: 15px;
  max-width: 550px;
  margin: 20px auto;
  box-shadow: 0 15px 15px rgba(0,0,0,.1);
}

#drum-machine #display {
  color: green;
  font-weight: 500;
  max-width: 200px;
  height: 55px;
  line-height: 55px;
  margin: auto;
  text-align: center;
  background-color: #eee;
}

#keyboard {
  display: flex;
  margin-top: 15px;
  flex-wrap: wrap;
}

#keyboard > div {
  flex-grow: 1;
  height: 100px;
  background-color: #4CAF50;
  margin: 1%;
  width: 30%;
  cursor: pointer;
  text-align: center;
  line-height: 100px;
  box-shadow: 0 10px 15px rgba(0,0,0,.1);
}

#keyboard > div:hover {
  background-color: #388E3C;
}

#keyboard > div:active {
  background-color: #FFC107;
}

#keyboard > div > span{
  color: #FFC107;
  font-size: 2em;
  text-transform: uppercase;
  font-weight: 500;
}

#keyboard > div:active > span {
  color: #388E3C;
}
